<template>
    <div>
        <div class="fl">
            <div class="pagination">
                <div class="divItem">
                    <a href="javascript:;" @click.stop="gobackpage"></a>
                </div>
                <div class="divItem pages">
                </div>
                <div class="divItem">
                    <a href="javascript:;" @click.stop="showGoto"></a>
                </div>
            </div>
        </div>
        <div class="goto">
            <div class="gotoClose">
                <span @click.stop="closeGoto"></span>
            </div>
            <div class="doGoto">
                <span>
                    
                    <input type="text" v-model="gotoPageNum">
                    <button @click.stop="gotoPage"></button>
                </span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['pageObj'],
    data() {
        return {
            totalPage: 0,
            currentPage: 0,
            gotoPageNum: 1
        }
    },
    mounted() {
        // console.log(this.pageObj);
        this.setTotalPage()
        this.setPage()
        this.setAStyle()
    },
    methods: {
        setTotalPage() {
            this.totalPage = Math.ceil(this.pageObj.totalCount / this.pageObj.pageNum)
            // console.log(this.totalPage);
        },

        // 
        setPage() {
            // 77
            let pageSize = 0
            pageSize = this.totalPage >= 7 ? 7 : this.totalPage

            if(pageSize > 0) {
                // 
                let pages = document.querySelector('.pages')
                const vueScopedStyleAttr = pages.attributes[0].name
                for(let i = 0; i < pageSize; i++) {
                    let a = document.createElement('a')
                    a.setAttribute(vueScopedStyleAttr, '')
                    a.setAttribute('index', i)
                    a.href = 'javascript:;'
                    a.textContent = i + 1
                    a.addEventListener('click', (e) => {
                        this.editPagesStyle(e)
                    })
                    if(i === 0) {
                        a.classList.add('checkedPage')
                    }
                    pages.appendChild(a)
                }
            }
        },

        // 
        editPagesStyle(e) {
            // adiv
            // diva

            // let index = e.target.getAttribute('index')
            // let num = e.target.textContent
            
            let index = e.target.parentNode.getAttribute('index')
            let num = e.target.parentNode.textContent
            // console.log(e.target.parentNode);
            this.pagenation(num,index)
        },

        // index
        pagenation(currentPage, i) {
            let as = document.querySelector('.pages').querySelectorAll('a')
            // index
            let cn = currentPage - 0
            let cindex = i

            // console.log(cn);
            // console.log(cindex);

            // 7
            if(as.length < 7) {
                as.forEach(a => {
                    a.classList.remove('checkedPage')
                })
                as[i].classList.add('checkedPage')

                cn = cn >= this.totalPage ? this.totalPage : cn
            } else {
                let index = 3

                // 
                let pageMax = 0
                // 
                let midMax = this.totalPage - 3
                // 2
                as.forEach(a => {
                    a.classList.remove('checkedPage')
                })
                // 3.1index
                index = cindex
                // 3.2index
                as[index].classList.add('checkedPage')

                 // 3.3
                if (cindex >= 3) {
                    as.forEach(a => {
                        a.classList.remove('checkedPage')
                    })
                    as[3].classList.add('checkedPage')

                    // 
                    if (cn > midMax) {
                        cn == midMax + 1 ? pageMax = cn + 2 : null
                        cn == midMax + 2 ? pageMax = cn + 1 : null
                        cn == midMax + 3 ? pageMax = cn : null
                        as.forEach(a => {
                            a.classList.remove('checkedPage')
                        })
                        as[index].classList.add('checkedPage')
                    } else {
                        pageMax = cn + 3
                    }
                } else {
                    if (cn < midMax) {
                        //  = 
                        let a = 6 - index
                        pageMax = cn + a
                    }
                }

                // 4tdsa()
                for(let i = as.length - 1; i >= 0; i--) {
                    as[i].textContent = pageMax
                    pageMax --
                    if ((as[i].textContent - 0) == cn) {
                        as.forEach(a => {
                            a.classList.remove('checkedPage')
                        })
                        as[i].classList.add('checkedPage')
                    }
                }

                // 5indexpageMax
                index = 3
                pageMax = 0
            }

            // data
            this.currentPage = cn

            // 
            this.$emit('getCurrentpage', this.currentPage)
        },

        // 
        gobackpage() {
            // a checkedPage
            let num = document.querySelector('.checkedPage').textContent
            let index = document.querySelector('.checkedPage').getAttribute('index')

            let as = document.querySelector('.pages').querySelectorAll('a')

            // 1
            if( num != 1) {
                index -= 1
                num -= 1

                // index0
                index = index < 0 ? index = 0 : index

                // console.log(num);
                // console.log(index);

                // data
                this.currentPage = num

                // 
                this.pagenation(num,index)
            }

            
        },

        // 
        showGoto(e) {
            let goto = document.querySelector('.goto')
            goto.style.display = 'block'
            goto.style.left = (e.pageX + 5) + 'px'
            goto.style.top = (e.pageY + 5) + 'px'
        },

        // 
        gotoPage() {
            // pageNum
            // cn
            // index
            let pageNum = this.gotoPageNum
            let cn = 0
            let index = 0

            // 7(7)
            if(this.totalPage < 7) {
                pageNum >= this.totalPage ? cn = this.totalPage : cn = pageNum
                if(cn < 1) {
                    cn = 1
                }
                index = cn - 1
            } else {
                // 
                const mn = this.totalPage - 3
                if( pageNum > 1) {
                    cn = pageNum >= this.totalPage ? this.totalPage : pageNum
                } else {
                    cn = 1
                }

                this.gotoPageNum = cn

                if(cn < mn && cn > 3) index = 3
                if(cn < 4) index = (cn - 1)
                if(cn > mn) {
                    cn == mn + 1 ? index = 3 + 1 : null
                    cn == mn + 2 ? index = 3 + 2 : null
                    cn == mn + 3 ? index = 3 + 3 : null
                }
            }

            // data
            this.currentPage = cn - 0
            
            // console.log('cn' + cn);
            // console.log('index' + index);
            this.pagenation(cn, index)

            document.querySelector('.goto').style.display = 'none'
        },

        // 
        closeGoto() {
            document.querySelector('.goto').style.display = 'none'
        },

        // a
        setAStyle() {
            // absolute
            let as = document.querySelector('.pagination').querySelectorAll('a')
            as.forEach(a => {
                // div 
                a.addEventListener('mouseenter', function() {
                    // a
                    const aw = a.offsetWidth
                    const ah = a.offsetHeight
                    a.style.position = 'relative'

                    // a
                    let mask = document.createElement('div')
                    mask.style.width = aw + 'px'
                    mask.style.height = ah + 'px'
                    mask.style.backgroundColor = 'rgba(255,255,255,.4)'
                    mask.style.position = 'absolute'
                    mask.style.top = 0
                    mask.style.left = 0
                    mask.className = 'mask'

                    // a
                    a.appendChild(mask)
                })

                // 
                a.addEventListener('mouseleave',function() {
                    a.querySelector('.mask').remove()
                })
            })
        },

        //
    }
}
</script>

<style lang="less" scoped>
a {
    color: var(--themColor);
}

// 750px
@media screen and (max-width: 750px) {
    .pagination {
        height: 0.625rem!important;
        a {
            padding: 0 0.260417rem!important;
            line-height: 0.625rem;
            font-size: 0.260417rem!important;
        }
    }
}

table {
    border-collapse: collapse;
}

.pagination {
    overflow: hidden;
    float: left;
    height: 45px;
    line-height: 45px;
    border: 1px solid #cacaca;
    box-shadow: 0 0 5px -3px #000;
    border-radius: 2.5px;
    padding: 1px;
    background-color: #fff0cd;
    .divItem {
        float: left;
        a {
            display: block;
            padding: 0 20px;
            float: left;
            border-right: 1px solid #ead5bc;
            font-size: 15px;
            font-weight: bold;
        }
    }
    .divItem:last-child a {
        border: none;
    }
}

.goto {
    display: none;
    position: fixed;
    width: 200px;
    height: 100px;
    background-color: #fff6df;
    border: 1px solid var(--themColor);
    box-shadow: 0 0 10px #444;

    .gotoClose {
        width: 100%;
        height: 30%;
        border-bottom: 1px solid #fff0cd;
        span {
            float: right;
            display: block;
            width: 25%;
            height: 100%;
            background-color: #fff0cd;
            text-align: center;
            font-size: 15px;
            font-weight: bold;
            line-height: 30px;
            cursor: pointer;
        }
        span:hover {
            background-color: var(--themColor);
            color: #fff;
        }
    }

    .doGoto {
        box-sizing: border-box;
        padding: 20px 0;
        padding-left: 5px;
        span {
            font-size: 15px;
            input {
                width: 65px;
                height: 25px;
                background-color: #fffcee;
                border-radius: 5px;
                border: 1px solid #e0c19e;
                box-shadow: 0 0 2px 0 #bbb inset;
            }
            button {
                width: 30px;
                height: 25px;
                margin-left: 5px;
                border: none;
                background-color: #ead5bc;
                border-radius: 5px;
            }
        }
    }
}

.checkedPage {
    background-color: #591804!important;
    color: #fff;
}
</style>